import { remove, render, RenderPosition } from '../framework/render.js';
import ListView from '../view/list-view';
import PointListEmptyView from '../view/point-list-empty-view.js';
import SortView from '../view/sort-view';
import TripInfoView from '../view/trip-info-view';
import PointPresenter from './point-presenter.js';
import { FilterType, SortType, UpdateType, UserAction } from '../const.js';
import {sortDay, sortPointPrice, sortPointTime } from '../utils/formatting-date.js';
import { filter } from '../utils/filter.js';
import PointNewPresenter from './point-new-presenter.js';
import LoadingView from '../view/loading-view.js';
import UiBlocker from '../framework/ui-blocker/ui-blocker.js';

const TimeLimit = {
  LOWER_LIMIT: 350,
  UPPER_LIMIT: 1000,
};
export default class TripPresenter {
  #pointListComponent = new ListView();

  #pointEmptyComponent=null;
  #tripInfoComponent = null;

  #pointsModel = null;
  #mainContainer = null;
  #headerContainer = null;
  #filterModel=null;
  #loadingComponent=new LoadingView();
  #offersModel=null;
  #destinationsModel=null;

  #pointPresenter=new Map();
  #pointNewPresenter=null;
  #currentSortType = SortType.DAY;
  #filterType=FilterType.EVERYTHING;
  #sortComponent=null;
  #isLoading = true;
  #uiBlocker = new UiBlocker(TimeLimit.LOWER_LIMIT, TimeLimit.UPPER_LIMIT);
  constructor(headerContainer, mainContainer, pointsModel, filterModel, offersModel, destinationsModel) {
    this.#headerContainer = headerContainer;
    this.#mainContainer = mainContainer;
    this.#pointsModel = pointsModel;
    this.#filterModel=filterModel;
    this.#offersModel=offersModel;
    this.#destinationsModel=destinationsModel;

    this.#pointNewPresenter=new PointNewPresenter(this.#pointListComponent.element, this.#handleViewAction,  this.#offersModel, this.#destinationsModel, pointsModel);
    this.#pointsModel.addObserver(this.#handleModelEvent);
    this.#filterModel.addObserver(this.#handleModelEvent);
  }

  get points() {
    this.#filterType = this.#filterModel.filter;
    const points = this.#pointsModel.points;
    const filteredPoints = filter[this.#filterType](points);

    switch (this.#currentSortType) {
      case SortType.TIME:
        return filteredPoints.sort(sortPointTime);
      case SortType.PRICE:
        return filteredPoints.sort(sortPointPrice);
    }
    return filteredPoints.sort(sortDay);
  }


  createPoint = (callback) => {

    this.#currentSortType = SortType.DAY;
    this.#filterModel.setFilter(UpdateType.MAJOR, FilterType.EVERYTHING);
    this.#pointNewPresenter.init(callback);
  };

  #renderPoint = (point, offers, destinations) => {

    const pointPresenter=new PointPresenter(this.#pointListComponent.element, this.#handleViewAction, this.#handleModeChange);


    pointPresenter.init(point, offers, destinations);
    this.#pointPresenter.set(point.id, pointPresenter);
  };

  #renderPoints = async() => {
    const offers = await this.#offersModel.get();
    const destinations=await this.#destinationsModel.get();

    this.points.forEach((point) => {

      this.#renderPoint(point, offers, destinations);
    });
  };

  #renderNoPoints = () => {
    this.#pointEmptyComponent=new PointListEmptyView(this.#filterType);
    render(this.#pointEmptyComponent, this.#mainContainer);
  };

  #renderLoading = () => {
    render(this.#loadingComponent, this.#mainContainer, RenderPosition.AFTERBEGIN);
  };

  #handleModeChange = () => {
    this.#pointNewPresenter.destroy();
    this.#pointPresenter.forEach((presenter) => presenter.resetView());
  };


  #handleViewAction = async (actionType, updateType, update) => {
    this.#uiBlocker.block();
    switch (actionType) {
      case UserAction.UPDATE_POINT:
        this.#pointPresenter.get(update.id).setSaving();
        try {
          await
          this.#pointsModel.updatePoint(updateType, update);
        } catch(err) {
          this.#pointPresenter.get(update.id).setAborting();
        }
        break;
      case UserAction.ADD_POINT:
        this.#pointNewPresenter.setSaving();
        try {
          await
          this.#pointsModel.addPoint(updateType, update);
        } catch(err) {
          this.#pointNewPresenter.setAborting();
        }
        break;
      case UserAction.DELETE_POINT:
        this.#pointPresenter.get(update.id).setDeleting();
        try {
          await
          this.#pointsModel.deletePoint(updateType, update);
        } catch(err) {
          this.#pointPresenter.get(update.id).setAborting();
        }
        break;
    }
    this.#uiBlocker.unblock();
  };


  #handleModelEvent = (updateType, data) => {
    switch (updateType) {
      case UpdateType.PATCH:
        // -    (,   )
        this.#pointPresenter.get(data.id).init(data);
        break;
      case UpdateType.MINOR:
        // -   (,     )
        this.#clearTrip();
        this.#renderTrip();
        break;
      case UpdateType.MAJOR:
        this.#clearTrip({resetSortType: true});
        this.#renderTrip();
        // -    (,   )
        break;
      case UpdateType.INIT:
        this.#isLoading = false;
        remove(this.#loadingComponent);
        remove(this.#tripInfoComponent);
        this.#renderTrip();
        break;
    }
  };

  #handleSortTypeChange = (sortType) => {
    if (this.#currentSortType === sortType) {
      return;
    }


    this.#currentSortType=sortType;
    // -  
    // -   

    this.#clearTrip();
    this.#renderTrip();

  };

  #renderSort = () => {
    this.#sortComponent = new SortView(this.#currentSortType);
    this.#sortComponent.setSortTypeChangeHandler(this.#handleSortTypeChange);
    render(this.#sortComponent, this.#mainContainer, RenderPosition.AFTERBEGIN);
  };


  #clearTrip = ({resetSortType = false} = {}) => {
    this.#pointNewPresenter.destroy();
    this.#pointPresenter.forEach((presenter) => presenter.destroy());
    this.#pointPresenter.clear();
    remove(this.#tripInfoComponent);
    remove(this.#sortComponent);
    remove(this.#loadingComponent);
    if (this.#pointEmptyComponent) {
      remove(this.#pointEmptyComponent);
    }

    if (resetSortType) {
      this.#currentSortType = SortType.DAY;
    }
  };

  #renderTrip = () => {
    const points=this.points;
    const pointCount=points.length;
    this.#tripInfoComponent = new TripInfoView(this.points);
    render(this.#tripInfoComponent, this.#headerContainer, RenderPosition.AFTERBEGIN);
    if (pointCount === 0) {
      this.#renderNoPoints();
      return;
    }
    this.#renderSort();

    render(this.#pointListComponent, this.#mainContainer);
    if (this.#isLoading) {
      this.#renderLoading();
      return;
    }

    this.#renderPoints();
  };
}
