import {FilterType} from '../const.js';

import {isPointExpired, isPointExpiringToday} from './formatting-date.js';

const filter = {
  [FilterType.EVERYTHING]: (points) => points.filter((point) => !point.isArchive),
  [FilterType.FUTURE]: (points) => points.filter((point) =>  !isPointExpired(point.dateFrom) && !isPointExpiringToday(point.dateFrom)),
  [FilterType.PAST]: (points) => points.filter((point) => isPointExpired(point.dateTo)),
};

export {filter};
