import { nanoid } from 'nanoid';
import AbstractStatefulView from '../framework/view/abstract-stateful-view.js';
import { createEventDetailsTemplate } from './event-details-template.js';
import { createPointHeaderTemplate } from './point-header-template.js';
import flatpickr from 'flatpickr';

import 'flatpickr/dist/flatpickr.min.css';

const BLANK_POINT = {
  basePrice: 0,
  dateFrom: new Date(),
  dateTo: new Date(),
  destination: {
    description: '',
    name: '',
    pictures: { src: '', description: '' },
  },
  id: null,
  isFavorite: false,
  offers: [],
  type: 'taxi',
};


const createPointEditTemplate = (point, offers, destinations, id) =>{
  if (!point.id)
  {point.id=id;}

  return `<li class='trip-events__item'><form class="event event--edit" action="#" method="post">
  ${createPointHeaderTemplate(point, destinations)}
  ${createEventDetailsTemplate(point, offers)}
  </form></li>
  `;
};


export default class EditView extends AbstractStatefulView {
  #datepicker = null;
  #prevCity=null;
  #offers=null;
  #destinations=null;
  #pointCity=null;
  #point=null;
  #id=null;
  constructor( offers, destinations, id, point = BLANK_POINT) {
    super();
    this.#offers=offers;
    this.#destinations=destinations;
    this.#id=id;
    this._state=EditView.parsePointToState(point);
    this.#prevCity=this._state;
    this.#setInnerHandlers();
    this.#setDatepicker();
    this.#pointCity=destinations.map((elem)=> elem.name);
    this.#point=point;

  }

  get template() {
    return createPointEditTemplate(this._state, this.#offers, this.#destinations, this.#id);
  }

  removeElement = () => {
    super.removeElement();

    if (this.#datepicker) {
      this.#datepicker.destroy();
      this.#datepicker = null;
    }
  };

  setFormSubmitHandler = (callback) => {
    this._callback.formSubmit = callback;
    this.element
      .querySelector('form')
      .addEventListener('submit', this.#formSubmitHandler);
  };

  setDeleteClickHandler = (callback) => {
    this._callback.deleteClick = callback;
    this.element.querySelector('.event__reset-btn').addEventListener('click', this.#formDeleteClickHandler);
  };

  _restoreHandlers = () => {
    this.#setInnerHandlers();
    this.setFormSubmitHandler(this._callback.formSubmit);
    this.#setDatepicker();
    this.setDeleteClickHandler(this._callback.deleteClick);
  };

  #formSubmitHandler = (evt) => {
    evt.preventDefault();
    this._callback.formSubmit(EditView.parseStateToPoint(this._state));
  };

  #formDeleteClickHandler = (evt) => {
    evt.preventDefault();
    this._callback.deleteClick(EditView.parseStateToPoint(this._state));
  };

  #setInnerHandlers = () => {
    this.element.querySelector('.event__type-group')
      .addEventListener('input', this.#typeChangeHandler);
    this.element.querySelector('.event__input--destination')
      .addEventListener('input', this.#descriptionInputHandler);
    this.element.querySelector('.event__input--price')
      .addEventListener('input', this.#priceInputHandler);

    const eventAvailable=this.element.querySelector('.event__available-offers');
    if(eventAvailable)
    {eventAvailable.addEventListener('change', this.#offerChangeHandler);}
  };

  #typeChangeHandler = (evt) => {
    evt.preventDefault();
    this._setState({
      type:evt.target.value,
      offers: []
    });
    this.updateElement({
      type: evt.target.value,
      offers:[]
    });
  };

  #descriptionInputHandler = (evt) => {
    evt.preventDefault();
    if  (this.#prevCity.destination.name=== evt.target.value)
    { this._setState({
      destination: {...this.#prevCity.destination  }
    });
    this.updateElement({
      destination: {...this.#prevCity.destination}
    });
    }

    if (this.#pointCity.includes(evt.target.value)&& this.#prevCity.destination.name!== evt.target.value)
    {
      const destination=this.#destinations.find((elem)=> elem.name===evt.target.value);
      this._setState({
        destination: {...this._state.destination,   description: destination.description,
          name: evt.target.value,
          pictures: destination.pictures }
      });
      this.updateElement({
        destination: {...this._state.destination,   description: destination.description,
          name: evt.target.value,
          pictures: destination.pictures}
      });
    }
  };

  #priceInputHandler=(evt)=>{
    evt.preventDefault();
    this._setState({
      basePrice:Number(evt.target.value)
    });

    this.updateElement({
      basePrice:Number(evt.target.value)
    });
  };

  #offerChangeHandler=(evt)=>{
    evt.preventDefault();


    const title=evt.target.nextElementSibling.children[0].innerHTML;
    const {offers: offers_} =this.#offers.find((elem)=> elem.type===this._state.type);
    const offer=offers_.find((elem)=> elem.title===title);

    const {id}=offer;

    if (evt.target.checked)
    {
      this.updateElement({
        offers:  [...this._state.offers, id
        ],
      });


      return;
    }
    this.updateElement({
      offers:  [...this._state.offers.filter((elem)=>elem!==id),
      ],
    });
  };


  static parsePointToState = (point) => ({...point,
    isDisabled: false,
    isSaving: false,
    isDeleting: false,
  });

  static parseStateToPoint = (state) => {
    const point = {...state};
    delete point.isDisabled;
    delete point.isSaving;
    delete point.isDeleting;
    return point;
  };

  reset = (point) => {
    this.updateElement(
      EditView.parsePointToState(point),
    );
  };

  #dueDateChangeHandler = ([minDate,maxDate]) => {

    this.updateElement({
      dateFrom: minDate,
      dateTo: maxDate,
    });
  };

  #setDatepicker = () => {
    if (this._state.dateFrom) {

      this.#datepicker = flatpickr(
        this.element.querySelector('.event__field-group--time'),
        {

          mode: 'range',
          dateFormat: 'Y-m-d h:i K',
          defaultDate: [this._state.dateFrom, this._state.dateTo],
          onChange: this.#dueDateChangeHandler,
        },
      );
    }
  };
}
