import { SortType } from '../const.js';
import AbstractView from '../framework/view/abstract-view.js';

const createSortTemplate = (currentSortType) => `<form class="trip-events__trip-sort  trip-sort" action="#" method="get">
<div class="trip-sort__item  trip-sort__item--${SortType.DAY}">
  <input id="sort-day" class="trip-sort__input  visually-hidden" type="radio" name="trip-sort" value="sort-${SortType.DAY}" ${currentSortType === SortType.DAY ? 'checked' : ''}>
  <label class="trip-sort__btn" for="sort-${SortType.DAY}"  data-sort-type="${SortType.DAY}">Day</label>
</div>

<div class="trip-sort__item  trip-sort__item--${SortType.EVENT}">
  <input id="sort-event" class="trip-sort__input  visually-hidden" type="radio" name="trip-sort" value="sort-${SortType.EVENT}" disabled>
  <label class="trip-sort__btn" for="sort-${SortType.EVENT}" data-sort-type="${SortType.EVENT}">Event</label>
</div>

<div class="trip-sort__item  trip-sort__item--${SortType.TIME}">
  <input id="sort-time" class="trip-sort__input  visually-hidden" type="radio" name="trip-sort" value="sort-${SortType.TIME}" ${currentSortType === SortType.TIME ? 'checked' : ''}>
  <label class="trip-sort__btn" for="sort-${SortType.TIME}" data-sort-type="${SortType.TIME}">Time</label>
</div>

<div class="trip-sort__item  trip-sort__item--${SortType.PRICE}">
  <input id="sort-price" class="trip-sort__input  visually-hidden" type="radio" name="trip-sort" value="sort-${SortType.PRICE}" ${currentSortType === SortType.PRICE ? 'checked' : ''}>
  <label class="trip-sort__btn" for="sort-${SortType.PRICE}" data-sort-type="${SortType.PRICE}">Price</label>
</div>

<div class="trip-sort__item  trip-sort__item--${SortType.OFFERS}">
  <input id="sort-offer" class="trip-sort__input  visually-hidden" type="radio" name="trip-sort" value="sort-${SortType.OFFERS}" disabled >
  <label class="trip-sort__btn" for="sort-${SortType.OFFERS}" data-sort-type="${SortType.OFFERS}">Offers</label>
</div>
</form>`;

export default class SortView extends AbstractView {
  #currentSortType = null;

  constructor(currentSortType) {
    super();
    this.#currentSortType = currentSortType;
  }

  get template() {
    return createSortTemplate(this.#currentSortType);

  }

  setSortTypeChangeHandler = (callback) => {
    this._callback.sortTypeChange = callback;
    this.element.addEventListener('click', this.#sortTypeChangeHandler);
  };

  #sortTypeChangeHandler = (evt) => {
    if ((evt.target.tagName !== 'LABEL')||(evt.target.control.disabled)) {
      return;
    }

    //  evt.preventDefault();
    this._callback.sortTypeChange(evt.target.dataset.sortType);
  };

}


