import AbstractView from '../framework/view/abstract-view.js';
import {
  compare,
  humanizePointDateMonth,
  humanizePointDueDate,
  humanizePointDueDay,
} from '../utils/formatting-date.js';

const createTripInfoTemplate = (points) => {
  const sumBasePrice = points.reduce((acc, elem) => acc + Number(elem.basePrice), 0);
  const minDate = points.map((elem) =>elem.dateFrom).sort(compare).map((elem)=> humanizePointDueDate(elem))[0];
  const maxDate = points.map((elem) =>elem.dateTo).sort(compare).map((elem)=> humanizePointDueDate(elem))[points.length-1];

  const tripInfoDates =
    humanizePointDateMonth(minDate) === humanizePointDateMonth(maxDate)
      ? `${minDate  } &nbsp;&mdash;&nbsp; ${  humanizePointDueDay(maxDate)}`
      : `${minDate  } &nbsp;&mdash;&nbsp; ${  maxDate}`;
  const arrCity = points.map((elem) => elem.destination.name);
  const uniqueCity = Array.from(new Set(arrCity));
  let tripInfoTitle;
  if (uniqueCity.length > 3)
  {tripInfoTitle =
      `${uniqueCity[0]  } &mdash;...&mdash; ${  uniqueCity[uniqueCity.length - 1]}`;}
  else if (uniqueCity.length > 2)
  {tripInfoTitle =
      `${uniqueCity[0]
      } &mdash; ${
        uniqueCity[1]
      } &mdash; ${
        uniqueCity[uniqueCity.length - 1]}`;}
  else if (uniqueCity.length > 1)
  {tripInfoTitle = `${uniqueCity[0]  } &mdash; ${  uniqueCity[1]}`;}
  else {tripInfoTitle = uniqueCity[0];}
  return ` <section class="trip-main__trip-info  trip-info">
<div class="trip-info__main">
  <h1 class="trip-info__title">${tripInfoTitle ? tripInfoTitle : 'empty trip' }</h1>

  <p class="trip-info__dates">${tripInfoDates}</p>
</div>

<p class="trip-info__cost">
  Total: &euro;&nbsp;<span class="trip-info__cost-value">${sumBasePrice}</span>
</p>
</section>`;
};

export default class TripInfoView extends AbstractView {
  #points = null;
  constructor(points) {
    super();
    this.#points = points;
  }

  get template() {
    return createTripInfoTemplate(this.#points);
  }
}
