import dayjs from 'dayjs';

const humanizePointDateMonth = (dueDate) => dayjs(dueDate).format('MM');
const humanizePointDueDay = (dueDate) => dayjs(dueDate).format('D');

const isPointExpiringToday = (dueDate) =>
  dueDate && dayjs(dueDate).isSame(dayjs(), 'D');
const humanizePointDueDate = (dueDate) => dayjs(dueDate).format('D MMM');
const humanizePointHourDate = (dueDate) => dayjs(dueDate).format('HH:mm');

const humanizePointFullDate = (dueDate) =>
  dayjs(dueDate).format('DD/MM/YY HH:mm');

const resultPointHourDate = (dateto, datefrom) => {
  const minute = Math.floor(dayjs(dateto).diff(datefrom, 'minute', true));
  const days = Math.floor(minute / (60 * 24));
  const hours = Math.floor((minute - days * 60 * 24) / 60);
  const minutes = Math.floor(minute - (hours * 60 + days * 60 * 24));
  let result = null;
  if (days) {result = `${result + days  }D `;}
  if (hours) {result = `${result + hours  }H `;}
  if (minutes) {result = `${result + minutes  }M `;}

  return result;
};


const sortMinute = (dateto, datefrom) => Math.floor(dayjs(dateto).diff(datefrom, 'minute', true));
const sortPointTime = (pointA, pointB) => {

  const datePointA = sortMinute(pointA.dateTo, pointA.dateFrom);
  const datePointB = sortMinute(pointB.dateTo, pointB.dateFrom);

  if (datePointA > datePointB) {
    return -1;
  }
  if (datePointA < datePointB) {
    return 1;
  }
  // a    b
  return 0;
};

const sortDay = (a, b) => {

  const dateA=b.dateFrom;
  const dateB=a.dateFrom;

  if (dateA === null && dateB === null) {
    return 0;
  }

  if (dateA === null) {
    return 1;
  }

  if (dateB === null) {
    return -1;
  }

  if (dateA > dateB) {
    return -1;
  }
  if (dateA < dateB) {
    return 1;
  }
  // a    b
  return 0;
};
const sortPointPrice = (pointA, pointB) => {
  if (pointA.basePrice > pointB.basePrice) {
    return -1;
  }
  if (pointA.basePrice < pointB.basePrice) {
    return 1;
  }
  // a    b
  return 0;
};
const compare=(b,a)=>{
  const dateA = new Date(a);
  const dateB = new Date(b);

  return dateB - dateA;
};

const isPointExpired = (dueDate) => dueDate && dayjs().isAfter(dueDate, 'D');
export {
  humanizePointDueDay,
  humanizePointDateMonth,
  humanizePointDueDate,
  humanizePointHourDate,
  resultPointHourDate,
  humanizePointFullDate,
  isPointExpiringToday,
  sortPointPrice,
  isPointExpired,
  sortPointTime,
  sortDay,
  compare
};
