import { createPointDestinationTemplate } from './point-destination-template';
import { createPointOffersTemplate } from './point-offers-template';

export const createEventDetailsTemplate = (point, offers) => {
  const {destination}= point;
  return `
<section class="event__details">
${createPointOffersTemplate(point, offers)}
${createPointDestinationTemplate(destination)}
</section>`;
};
