
import AbstractView from '../framework/view/abstract-view.js';

import {
  humanizePointDueDate,
  resultPointHourDate,
} from '../utils/formatting-date.js';
const createPointTemplate = (point, offer) => {
  const {
    basePrice,
    dateFrom,
    dateTo,
    destination,
    isFavorite,
    offers,
    type,
  } = point;
  const { name } = destination;
  const offersByType=offer.offers;
  const pointOffers=offersByType.filter((elem)=>offers.includes(elem.id));

  const active = isFavorite ? 'event__favorite-btn--active' : '';
  const dateTrip = resultPointHourDate(dateTo, dateFrom);
  return `<li class="trip-events__item">
<div class="event">
  <time class="event__date" datetime="${dateFrom}">${humanizePointDueDate(dateFrom)}</time>
  <div class="event__type">
    <img class="event__type-icon" width="42" height="42" src="img/icons/${type}.png" alt="Event type icon">
  </div>
  <h3 class="event__title">${`${type  } ${name}`}</h3>
  <div class="event__schedule">
    <p class="event__time">
      <time class="event__start-time" datetime="${dateFrom}">${humanizePointDueDate(dateFrom)}</time>
      &mdash;
      <time class="event__end-time" datetime="${dateTo}">${humanizePointDueDate(dateTo)}</time>
    </p>
    <p class="event__duration">${dateTrip}</p>
  </div>
  <p class="event__price">
    &euro;&nbsp;<span class="event__price-value">${basePrice}</span>
  </p>
   ${
  offers.length
    ? `
    <h4 class="visually-hidden">Offers:</h4>

    <ul class="event__selected-offers">
    ${pointOffers
    .map(
      (elem) => `<li class="event__offer">
    <span class="event__offer-title">${elem.title}</span>
    &plus;&euro;&nbsp;
    <span class="event__offer-price">${elem.price}</span>
  </li>`
    )
    .toString()
    .split(',')
    .join('')}
    </ul>
    `
    : ''
}

  <button class="event__favorite-btn ${active}" type="button">
    <span class="visually-hidden">Add to favorite</span>
    <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
      <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
    </svg>
  </button>
  <button class="event__rollup-btn" type="button">
    <span class="visually-hidden">Open event</span>
  </button>
</div></li>`;
};

export default class PointView extends AbstractView {
  #point = null;
  #offer=null;

  constructor(point, offer) {
    super();
    this.#point = point;
    this.#offer=offer;
  }

  get template() {
    return createPointTemplate(this.#point, this.#offer);
  }

  setEditClickHandler = (callback) => {
    this._callback.editClick = callback;
    this.element
      .querySelector('.event__rollup-btn')
      .addEventListener('click', this.#editClickHandler);
  };

  #editClickHandler = (evt) => {
    evt.preventDefault();
    this._callback.editClick();
  };

  setFavoriteClickHandler = (callback) => {
    this._callback.favoriteClick = callback;
    this.element.querySelector('.event__favorite-btn').addEventListener('click', this.#favoriteClickHandler);
  };

  #favoriteClickHandler = (evt) => {
    evt.preventDefault();
    this._callback.favoriteClick();
  };
}
