

export const createPointOffersTemplate = (point, offersByTypes) => {
  const { offers } = point;
  const offersByType=offersByTypes.find((elem)=> elem.type===point.type);

  const pointOffers=offersByType.offers;
  if (!pointOffers.length) {
    return '';}
  return `<section class="event__section  event__section--offers">
<h3 class="event__section-title  event__section-title--offers">Offers</h3>

<div class="event__available-offers">
  ${pointOffers.map((of) => {
    const offer = of.id;
    const checked = offers.filter((item) => item === of.id).length
      ? 'checked'
      : '';
    return `<div class="event__offer-selector">
<input class="event__offer-checkbox  visually-hidden" id="event-offer-${offer}-1" type="checkbox" name="event-offer-${offer}" ${checked}>
<label class="event__offer-label" for="event-offer-${offer}-1">
<span class="event__offer-title">${of.title}</span>
&plus;&euro;&nbsp;
<span class="event__offer-price">${of.price}</span>
</label>
</div>`;
  }).join('')}
</div>
</section>
`;
};
