import TripPresenter from './presenter/trip-presenter.js';
import PointsModel from './model/points-model.js';

import FilterModel from './model/filter-model.js';
import FilterPresenter from './presenter/filter-presenter.js';
import PointsApiService from './point-api-service.js';


import OffersApiService from './offers-api-service.js';
import OffersModel from './model/offers-model.js';
import DestinationsModel from './model/destinations-model.js';
import DestinationsApiService from './destinations-api-service.js';


const AUTHORIZATION = 'Basic hS25sfS44wwcl12j';
const END_POINT = 'https://17.ecmascript.htmlacademy.pro/big-trip';

const pointsModel = new PointsModel(new PointsApiService(END_POINT, AUTHORIZATION));
const offersModel = new OffersModel(new OffersApiService(END_POINT, AUTHORIZATION));
const destinationsModel = new DestinationsModel(new DestinationsApiService(END_POINT, AUTHORIZATION));
const filterModel = new FilterModel();


const bodyElement = document.querySelector('body');
const siteHeaderElement = bodyElement.querySelector('.page-header');
const siteMainElement = bodyElement.querySelector('.page-main');
const tripFilters=siteHeaderElement.querySelector('.trip-controls__filters');
const tripMain=siteHeaderElement.querySelector('.trip-main');
const tripEvents=siteMainElement.querySelector('.trip-events');
const newPointButton=tripMain.querySelector('.trip-main__event-add-btn');
const tripPresenter=new TripPresenter(tripMain, tripEvents, pointsModel, filterModel, offersModel, destinationsModel);
const filterPresenter = new FilterPresenter(tripFilters, filterModel, pointsModel);


const handleNewPointFormClose = () => {
  newPointButton.disabled = false;
};

const handleNewPointButtonClick = () => {
  tripPresenter.createPoint(handleNewPointFormClose);
  newPointButton.disabled = true;
};

newPointButton.addEventListener('click', handleNewPointButtonClick);

filterPresenter.init();
pointsModel.init();

